import uniPopup from "@dcloudio/uni-ui/lib/uni-popup/uni-popup.vue"
export default {
	components:{uniPopup},
    data() {
        return {
			msg:'',
            background: ['color1', 'color2', 'color3'],
            indicatorDots: true,
            autoplay: true,
            interval: 5000,
            duration: 500,
			contract:[
				{
					contractTitle:'森帛合同森帛合同森帛合同森帛合同森帛合同森帛合同森帛合同森帛合同森帛合同',
					month:'3',
					status:'施工方核算'
				},
				{
					contractTitle:'森帛',
					month:'12',
					status:'我方方核算'
				},
				{
					contractTitle:'森帛',
					month:'12',
					status:'我方方核算'
				},
				{
					contractTitle:'森帛',
					month:'12',
					status:'我方方核算'
				},
				{
					contractTitle:'森帛',
					month:'12',
					status:'我方方核算'
				}
			]
        }
    },
	onShow() {
		if(this.hasLogin){
			this.$store.dispatch('ACgetUserInfo').then(res => {
				if(res.data.status == 200){
					
				}else{
					
				}
			}).catch(err => {
				let that = this
				setTimeout(() => {
					uni.clearStorageSync();
					uni.navigateTo({
						url:'/pages/ucenter/login'
					})
					that.$refs.popup.close()
				},2000)
				this.msg = '获取用户信息失败，请重新登录'
				this.$refs.popup.open()
			})
		}
	},
	computed: {
		hasLogin() {
			return this.$store.getters.getHasLogin
		}
	},
    methods: {
		gotoLogin(){
			uni.navigateTo({
				url:'/pages/ucenter/login'
			})
		},
        changeIndicatorDots(e) {
            this.indicatorDots = !this.indicatorDots
        },
        changeAutoplay(e) {
            this.autoplay = !this.autoplay
        },
        intervalChange(e) {
            this.interval = e.target.value
        },
        durationChange(e) {
            this.duration = e.target.value
        }
    }
}
